body {
  background-image: linear-gradient(to right bottom, #a4c9ff, #89b8ff, #6ea7ff, #5395ff, #3483ff);
}

.App {
  padding: 0;
  margin: 10vh auto;
  width: 30vw;
  height: 80vh;
  background-size: cover;
  background-position: center;
  border-radius: 35px;
  -webkit-box-shadow: 7px 21px 53px 6px rgba(0,0,0,0.42);
  -moz-box-shadow: 7px 21px 53px 6px rgba(0,0,0,0.42);
  box-shadow: 7px 21px 53px 6px rgba(0,0,0,0.42);
  background-image: url("./images/mainsite.jpg");
  background-position: center;
}

div.mainDiv {
  margin: 0;
  padding: 0;
}

.App input, .App button {
  padding: 10px 20px;
  background-color: transparent;
  color: rgb(255, 255, 255);
  border: 1px solid white;
  transition: .3s;
}

input::placeholder{
  text-align: center;
  color: white;
}

input[type=text] 
{ text-align:center }

.App button {
  cursor: pointer;
}
.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}

@media only screen and (max-width: 1373px) { 
  .App {
    width: 35vw;
    height: 80vh;
  }
}

@media only screen and (max-width: 915px) { 
  .App {
    width: 45vw;
    height: 80vh;
  }
}

@media only screen and (max-width: 750px) { 
  .App {
    width: 55vw;
    height: 80vh;
  }
}

@media only screen and (max-width: 600px) { 
  .App {
    width: 100vw;
    height: 100vh;
    margin: 0;
    border-radius: 0;
  }
}