* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.background {
  padding-top: 30%;
  background-size: cover;
  padding-bottom: 250px;
}

.sun {
  background-image: url("./images/sun2.jpg");
  background-position: center;
}

.clouds {
  background-image: url("./images/cloud2.jpg");
  background-position: center;
}

.rain {
  background-image: url("./images/rain2.jpg");
  background-position: center;
}

.snow {
  background-image: url("./images/snow2.jpg");
  background-position: center;
}

.night {
  background-image: url("./images/night2.jpg");
  background-position: center;
}

.noActive {
  opacity: 0;
  transition: .5s ease-in-out;
}

.result h3, .result h4 {
  font-weight: normal;
}
.result em {
  font-weight: bold;
}

.data {
  margin-top: 2vh;
}

.data .time {
  font-size: 16px;
}

.icon {
  font-size: 100px;
  display: block;
  margin: 0 auto;
  text-align: center;
  color: white;
}

.data h3, .data h4 {
  color: white;
  text-align: center;
  font-size: 24px;
}

.images img{
  width: 100vw;
  height: 100%;
  opacity: 0;
  overflow: hidden;
}

.infoWeather {
  margin-top: 2vh;
  display: flex;
  justify-content: center;
  min-height: 10vh;
}

.infoWeather h4 {
  margin: auto 2vw;
  font-size: 84px;
  font-weight: bold;
}

.pressure, .wind {
  margin: 2vh 2vw;
  color: white;
  background-color: brown;
  text-align: center;
  padding: 25px 20px;
  border-radius: 50%;
}

.infoWeather p .icon {
  font-size: 28px;
}

.city {
  font-size: 34px;
}

.city::first-letter {
  text-transform: uppercase;
}

.empty {
  text-align: center;
  font-size: 32px;
  color: white;
  text-transform: uppercase;
  padding-top: 15vh;
}

@media only screen and (max-width: 1373px) {
  .pressure, .wind {
    padding: 30px 25px;
  }
}