body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.inputForm {
    margin-top: 2vh;
    text-align: center;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.background {
  padding-top: 30%;
  background-size: cover;
  padding-bottom: 250px;
}

.sun {
  background-image: url(/weatherapp/static/media/sun2.f3dba8bf.jpg);
  background-position: center;
}

.clouds {
  background-image: url(/weatherapp/static/media/cloud2.ab759f2d.jpg);
  background-position: center;
}

.rain {
  background-image: url(/weatherapp/static/media/rain2.13f8b091.jpg);
  background-position: center;
}

.snow {
  background-image: url(/weatherapp/static/media/snow2.c8805398.jpg);
  background-position: center;
}

.night {
  background-image: url(/weatherapp/static/media/night2.57f58215.jpg);
  background-position: center;
}

.noActive {
  opacity: 0;
  transition: .5s ease-in-out;
}

.result h3, .result h4 {
  font-weight: normal;
}
.result em {
  font-weight: bold;
}

.data {
  margin-top: 2vh;
}

.data .time {
  font-size: 16px;
}

.icon {
  font-size: 100px;
  display: block;
  margin: 0 auto;
  text-align: center;
  color: white;
}

.data h3, .data h4 {
  color: white;
  text-align: center;
  font-size: 24px;
}

.images img{
  width: 100vw;
  height: 100%;
  opacity: 0;
  overflow: hidden;
}

.infoWeather {
  margin-top: 2vh;
  display: flex;
  justify-content: center;
  min-height: 10vh;
}

.infoWeather h4 {
  margin: auto 2vw;
  font-size: 84px;
  font-weight: bold;
}

.pressure, .wind {
  margin: 2vh 2vw;
  color: white;
  background-color: brown;
  text-align: center;
  padding: 25px 20px;
  border-radius: 50%;
}

.infoWeather p .icon {
  font-size: 28px;
}

.city {
  font-size: 34px;
}

.city::first-letter {
  text-transform: uppercase;
}

.empty {
  text-align: center;
  font-size: 32px;
  color: white;
  text-transform: uppercase;
  padding-top: 15vh;
}

@media only screen and (max-width: 1373px) {
  .pressure, .wind {
    padding: 30px 25px;
  }
}
body {
  background-image: linear-gradient(to right bottom, #a4c9ff, #89b8ff, #6ea7ff, #5395ff, #3483ff);
}

.App {
  padding: 0;
  margin: 10vh auto;
  width: 30vw;
  height: 80vh;
  background-size: cover;
  background-position: center;
  border-radius: 35px;
  box-shadow: 7px 21px 53px 6px rgba(0,0,0,0.42);
  background-image: url(/weatherapp/static/media/mainsite.fd165458.jpg);
  background-position: center;
}

div.mainDiv {
  margin: 0;
  padding: 0;
}

.App input, .App button {
  padding: 10px 20px;
  background-color: transparent;
  color: rgb(255, 255, 255);
  border: 1px solid white;
  transition: .3s;
}

input::-webkit-input-placeholder{
  text-align: center;
  color: white;
}

input::-ms-input-placeholder{
  text-align: center;
  color: white;
}

input::placeholder{
  text-align: center;
  color: white;
}

input[type=text] 
{ text-align:center }

.App button {
  cursor: pointer;
}
.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}

@media only screen and (max-width: 1373px) { 
  .App {
    width: 35vw;
    height: 80vh;
  }
}

@media only screen and (max-width: 915px) { 
  .App {
    width: 45vw;
    height: 80vh;
  }
}

@media only screen and (max-width: 750px) { 
  .App {
    width: 55vw;
    height: 80vh;
  }
}

@media only screen and (max-width: 600px) { 
  .App {
    width: 100vw;
    height: 100vh;
    margin: 0;
    border-radius: 0;
  }
}
